import {React, Component} from 'react'
import '../style/scss/ComponentsStyles/UserPage.scss'
import userImg from '../img/userImg.jpg'


class UserPage extends Component {
    render() {
        return(
            <div className="mainApp-inner-activePage-userPage">
                <div className="userPage userPage-outer">
                    <div className="userPage-inner">
                        <aside className="userPage-inner-aside">
                            <figure>
                                <img src={userImg} alt="userImage"/>
                                <figcaption>გიგა ვარდია</figcaption>
                            </figure>
                        </aside>

                        <main className="userPage-inner-main">
                            <div className="userPage-inner-main-wrapper">
                                <div className="userPage-inner-main-wrapper-grid">
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სპეციალობა:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ინჟინერიის ბაკალავრი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        საფეხური:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                    ბაკალავრიატი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        მიმდინარე სემესტრი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                    5 სემესტრი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სქესი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                    მამრობითი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ეროვნება:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ქართველი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        დაბადების თარიღი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                    2000-05-30
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        საცხოვრებელი ადგილი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სენაკი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სტატუსი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        აქტიური
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სკოლა:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                    საინჟინრო ტექნოლოგიური ფაკულტეტი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სულ კრედიტები:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        133
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        GPA:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        2.95
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ტელეფონის ნომერი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item phoneInputWrapper">
                                        <div>
                                            <label>
                                                <input id="phoneInpId" type="number" step="any"/>
                                            </label>
                                        </div>
                                        

                                        <div>
                                            <button id="savePhoneBtn">
                                                შენახვა
                                            </button>
                                        </div>
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ჯგუფი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        2018-03
                                    </div>
                                </div>
                            </div>
                        </main>
                    </div>
                </div>
            </div>
        )
    }
}

export default UserPage;